@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500;700&display=swap);
html,
body,
#root,
.app {
  height: 100%;
  width: 100%;
  font-family: "Rubik", sans-serif;
}
.current{
    background-color: #f5f5f5;
    border: 1px solid #e5e5e5;
    border-radius: 3px;
    padding: 10px;
    margin: 5vh;
    color: black;
    width: 60vw;
}
.currentBody{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin-top: 3vh;
}
.forecast{
    display: flex;
    flex-direction:row;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto;
    width: 95vw;
    height: 100%;
    background-color: #e8d8d8;
    padding: 10px 20px;
    color: black;
    
}
.page {
    text-align: center;
    background-color: #081e4c;
    padding-top: 3%;
    font-size: 3rem;
    color: white;
  }

  .page-header {
    min-height: 10vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .cityText {
    font-size: 2rem;
    font-weight: 500;
    color: rgb(0, 0, 0);
    border-radius: 8px;
    border-color: rgb(19, 13, 7);
    border: 2px solid;
    padding: 1%;  
    width: 60vw;
  }
  
  .suggestionWrapper{
    min-height: 0vh;
    background-color: rgb(255, 255, 255);
    overflow-y: scroll;
    border: 1px solid rgb(209, 232, 238);
    border-radius: 8px;
  }
  .suggestion:hover{
    color: rgb(60, 141, 10);
    cursor: pointer;
  }

  .suggestion{
    background-color: rgb(225, 223, 223); 
    width: 60vw;
    color: black;
    min-height: 5vh;
    border: 1px solid rgb(221, 228, 230);
    }

  .app_body {
    margin-top: 5vh; 
    min-height: 100vh;
    display: flex;
    flex-direction: column; 
    align-items: center;
    font-size: calc(10px + 2vmin);
    color:white;
  }
  
  
body {
    margin: 0px;
}

.topic-container {
    display: grid;
    height: 100vh;
    grid-template-columns: 45% auto;
    margin-top: -1%;
}

.inp-fields {
    display: block;
    width: 100%;
    padding: 0.375rem 0.75rem;
    font-size: 1.2rem;
    font-weight: 500;
    line-height: 1.5;
    color: #0f0934;
    background-color: #fff;
    background-clip: padding-box;
    border: 2px solid #ced4da;
    -webkit-appearance: none;
    appearance: none;
    border-radius: 0.25rem;
    transition: border-color 0.5s ease-in-out, box-shadow 0.15s ease-in-out;
    margin-left: 18%;
}

.pwd-checklist {
    margin-left: 1%;
    margin-top: 1%;
}

